<template>
    <div class="studenthomework">
      <div>
        <h4>作业提交记录</h4>
      </div>
      <el-table
        :data="studenthomeworkList"
        style="width: 1700px;margin: 80px">
        <el-table-column
          prop="homeworkId"
          label="作业ID">
        </el-table-column>
        <el-table-column
          prop="homeworkTitle"
          label="作业名">
        </el-table-column>
        <el-table-column
          prop="homeworkContent"
          label="作业内容">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间">
        </el-table-column>
        <el-table-column
          prop="updateTime"
          label="提交时间">
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
    export default {
      props: {
      },
      data(){
        return{
          studenthomeworkList: [],
          user:{
            studentId:''
          }
        }
      },
      mounted() {
        this.getall();
      },
      methods:{
        getall(){
          this.user.studentId = this.$route.query.userId
          let that = this;
          this.$axios(
            {
              method: "post",
              url: "/studenthomework/bysid",
              data:that.user
            },
          ).then(function(res) {
            that.studenthomeworkList = res.data.data;
          });
        }
      }
    };
</script>



