<template>
  <div class="teacherhomework">
    <el-form :model="homework" label-width="100px" class="demo-ruleForm">
      <el-form-item label="作业名">
        <el-input v-model.number="homework.homeworkTitle"></el-input>
      </el-form-item>
      <el-form-item label="作业需求">
        <el-input v-model.number="homework.homeworkRequire"></el-input>
      </el-form-item>
      <el-form-item label="截止日期">
        <div class="block">
          <el-date-picker
            v-model="homework.finalTime"
            type="datetime"
            placeholder="选择日期时间"
            align="right"
            :picker-options="pickerOptions">
          </el-date-picker>
        </div>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="add()">提交</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    props: {
    },
    data(){
      return{
        homework: {
          teacherId:'',
          homeworkTitle: '',
          homeworkRequire:'',
          finalTime:''
        },
        pickerOptions: {
          shortcuts: [ {
            text: '明天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit('pick', date);
            }
          },{
            text: '后天',
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 2);
              picker.$emit('pick', date);
            },
          },
            {
              text: '三天后',
              onClick(picker) {
                const date = new Date();
                date.setTime(date.getTime() + 3600 * 1000 * 24*3);
                picker.$emit('pick', date);
              }
            },
            {
              disabledDate: (time) => {
                const date = new Date();
                return time.getTime() < date.getDate();//减去一天的时间代表可以选择同一天;
              }
            }
          ]
        },
      }
    },
    mounted() {
      this.homework.teacherId = this.$route.query.userId;
    },
    methods:{
      add(){
        let that = this;
        this.$axios(
          {
            method: "post",
            url: "/teacherhomework/createhomework",
            data: that.homework
          },
        ).then(function(res) {
          alert(res.data.data)
        });
      }
    }
  };
</script>



