<template>
  <div id="app">
    <div style="display: flex;justify-content: center;margin-top: 90px;">
      <el-card class="table_card">
        <div slot="header" class="clearfix">
          <span>合同管理系统用户注册</span>
        </div>
        <el-form
          :model="register"
          status-icon
          :rules="rules"
          ref="register"
          label-width="80px"
          class="demo-ruleForm"
        >
          <el-form-item label="ID:" prop="userId">
            <el-input v-model="register.userId" placeholder="请输入ID" autocomplete="off" clearable></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="userPassword">
            <el-input
              type="password"
              v-model="register.userPassword"
              placeholder="请输入密码"
              show-password
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item label="确认密码" prop="checkPassword">
            <el-input
              type="password"
              v-model="register.checkPassword"
              placeholder="请再次输入密码"
              show-password
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item label="姓名" prop="userName">
            <el-input type="text" v-model="register.userName" placeholder="请输入姓名" clearable></el-input>
          </el-form-item>
          <el-form-item label="类型">
            <el-radio-group v-model="register.userType">
              <el-radio label="0">学生</el-radio>
              <el-radio label="1">教师</el-radio>
            </el-radio-group>
          </el-form-item>
        </el-form>
        <div>
          <el-button style="width: 200px" type="success" @click="doLogin('register')">注册</el-button>
          <el-button style="width: 130px" type="primary" @click="jumpOnly('/login')">已有账号</el-button>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    let that=this;
    var checkEmail = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("邮箱不能为空"));
      }else{
        callback();
      }
    };
    var checkname = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("姓名不能为空"));
      } else {
        callback();
      }
    };
    var checkPassword = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    var againPassword = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请再次输入密码"));
      } else {
        if (value !== that.register.userPassword) {
          callback(new Error("两次输入密码不一致!"));
        }
        callback();
      }
    };
    return {
      register: {
        userId: "",
        userPassword: "",
        checkPassword: "",
        userName: "",
        userType:"0",
        code: ""
      },
      show: true, // 初始启用按钮
      count: "", // 初始化次数
      timer: "",

      rules: {
        userId: [{ validator: checkEmail, trigger: "blur" }],
        userPassword: [{ validator: checkPassword, trigger: "blur" }],
        checkPassword: [{ validator: againPassword, trigger: "blur" }],
        userName: [{ validator: checkname, trigger: "blur" }],
      }
    };
  },
  methods: {
    doLogin(register) {
      let that = this;
      this.$refs[register].validate(function(valid) {
        if (valid) {
          //ajax请求
          that.$axios(
              {
                //请求方式
                method: "post",
                //请求路劲
                url: "/register",
                //请求参数
                data: that.register
                //请求成功的回调函数
              }
            )
            .then(function(res) {

                that.$notify({
                  message: res.data.data,
                });
                if(res.data.data=="该账户已注册"||res.data.data=="注册失败")
                that.jump("/main");
            })
            .catch(function() {
              console.log("服务器异常");
            });
        }
      });
    },
    jump(path) {
      this.$router.push({ path: path, query: { userId: this.register.userId } });
    },
    jumpOnly(path) {
      this.$router.push(path);
    }
  }
};
</script>

<style>
#app {
  height: 100%;
  position: fixed;
  width: 100%;
  top: 0px;
  right: 0px;
}
.table_card {
  background-color: #ffffff8a;
  width: 390px;
}
.el-col {
  border-radius: 4px;
}
.image {
  position: fixed;
  left: 10px;
  top: 80px;
}
</style>



