<template>
    <div class="reviewhomework">
      <div>
        <h4>教师作业</h4>
      </div>
      <el-table
        :data="teacherhomeworkList"
        style="width: 1700px;margin: 100px">
        <el-table-column
          prop="homeworkId"
          label="作业ID">
        </el-table-column>
        <el-table-column
          prop="homeworkTitle"
          label="作业名">
        </el-table-column>
        <el-table-column
          prop="homeworkRequire"
          label="作业要求">
        </el-table-column>
        <el-table-column
          prop="createTime"
          label="创建时间">
        </el-table-column>
        <el-table-column
          prop="finalTime"
          label="截止时间">
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handle(scope.$index, scope.row)">查看</el-button>
          </template>
        </el-table-column>
      </el-table>

      <div>
        <h4>作业提交记录</h4>
      </div>
      <el-table
        :data="studenthomeworkList"
        style="width: 1700px;margin: 80px">
        <el-table-column
          prop="studentId"
          label="学生Id">
        </el-table-column>
        <el-table-column
          prop="homeworkTitle"
          label="作业名">
        </el-table-column>
        <el-table-column
          prop="homeworkContent"
          label="作业内容">
        </el-table-column>
        <el-table-column
          prop="updateTime"
          label="提交时间">
        </el-table-column>
      </el-table>
    </div>
</template>

<script>
  export default {
    props: {
    },
    data(){
      return{
        teacher:{
          teacherId:''
        },
        homework:{
          homeworkId:''
        },
        teacherhomeworkList: [],
        studenthomeworkList:[],
      }
    },
    mounted() {
      this.teacher.teacherId = this.$route.query.userId
      this.getall()
    },
    methods:{
      handle(index, row) {
        let that = this;
        that.homework.homeworkId = that.teacherhomeworkList[index].homeworkId
        that.$axios(
          {
            method: "post",
            url: "/studenthomework/bythid",
            data:that.homework
          },
        ).then(function(res) {
          that.studenthomeworkList=res.data.data;
        });
      },
      getall(){
        let that = this;
        that.$axios(
          {
            method: "post",
            url: "/teacherhomework/searchbyteacherid",
            data: that.teacher
          },
        ).then(function(res) {
          that.teacherhomeworkList=res.data.data;
        });
      },
      add(){
        let that = this;
        that.$axios(
          {
            method: "post",
            url: "/studenthomework/submit",
            data: that.studenthomework
          },
        ).then(function(res) {
          alert(res.data.data)
        });
      },
      update(){
        let that = this;
        that.$axios(
          {
            method: "post",
            url: "/studenthomework/update",
            data: that.studenthomework
          },
        ).then(function(res) {
          alert(res.data.data)
        });
      }
    }
  };
</script>



