import Vue from 'vue'
import Router from 'vue-router'
import Student from  '../components/student/student'
import StudentHomework from  '../components/studenthomework/studenthomework'
import TeacherHomework from  '../components/teacherhomework/teacherhomework'
import nav from "../components/nav/nav";
import handlehomework from "../components/studenthomework/handlehomework";
import login from "../components/login";
import register from "../components/register";
import reviewhomework from "../components/teacherhomework/reviewhomework";
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      name: 'login',
      component: login
    },
    {
      path:'/register',
      name: 'register',
      component: register
    },
    {
      path: '/main',
      name: 'nav',
      component: nav,
      children:[
        {
          path: '/student',
          name: 'student',
          component: Student
        },
        {
          path: '/studenthomework',
          name: 'studenthomework',
          component: StudentHomework
        },
        {
          path: '/reviewhomework',
          name: 'reviewhomework',
          component: reviewhomework
        },
        {
          path: '/teacherhomework',
          name: 'teacherhomework',
          component: TeacherHomework
        },
        {
          path:'/handlehomework',
          name: 'handlehomework',
          component: handlehomework
        }
      ]
    }
  ]
})



