<template>
  <div class="handlehomework">

    <div>
      <h4>作业列表</h4>
    </div>
    <el-table
      :data="teacherhomeworkList"
      style="width: 1700px;margin: 100px">
      <el-table-column
        prop="homeworkId"
        label="作业ID">
      </el-table-column>
      <el-table-column
        prop="teacherId"
        label="教师ID">
      </el-table-column>
      <el-table-column
        prop="homeworkTitle"
        label="作业名">
      </el-table-column>
      <el-table-column
        prop="homeworkRequire"
        label="作业要求">
      </el-table-column>
      <el-table-column
        prop="createTime"
        label="创建时间">
      </el-table-column>
      <el-table-column
        prop="finalTime"
        label="截止时间">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handle(scope.$index, scope.row)">提交</el-button>
        </template>
      </el-table-column>
    </el-table>

      <el-form :model="studenthomework" label-width="100px" class="demo-ruleForm" style="width:500px;margin: 30px">
        <el-form-item label="学号">
          <el-input v-model.number="studenthomework.studentId" disabled></el-input>
        </el-form-item>
        <el-form-item label="作业ID">
          <el-input v-model.number="studenthomework.homeworkId" disabled></el-input>
        </el-form-item>
        <el-form-item label="作业内容">
          <el-input v-model="studenthomework.homeworkContent"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button v-if="!exist" type="primary" @click="add()">提交</el-button>
          <el-button v-else type="primary" @click="update()">重新提交</el-button>
        </el-form-item>
      </el-form>

  </div>
</template>

<script>
  export default {
    props: {
    },
    data(){
      return{
        drawer:false,
        exist:false,
        studenthomework: {
          studentId: '',
          homeworkId: '',
          homeworkContent:''
        },
        teacherhomeworkList: [{
            homeworkId:1,
            teacherId:1,
            homeworkTitle:"",
            homeworkRequire:""
          }
        ],
        studenthomeworkList:[],
      }
    },
    mounted() {
      let that =this;
      that.studenthomework.studentId = that.$route.query.userId
      that.getall()
    },
    methods:{
      handle(index, row) {
        this.drawer=true
        this.studenthomework.homeworkId = this.teacherhomeworkList[index].homeworkId
        let that = this;
        that.$axios(
          {
            method: "post",
            url: "/studenthomework/byshid",
            data:that.studenthomework
          },
        ).then(function(res) {
          that.studenthomeworkList=res.data.data;
          if(that.studenthomeworkList.length>=1){
              that.studenthomework.homeworkContent= that.studenthomeworkList[0].homeworkContent;
              that.exist = true
            }else{
              that.exist = false
          }
        });
      },
      handleClose(done) {
        let that = this;
        this.$confirm('确认关闭？')
          .then(_ => {
            that.studenthomework.homeworkContent='';
            done()
          })
          .catch(_ => {});
      },
      getall(){
        let that = this;
        that.$axios(
          {
            method: "post",
            url: "/teacherhomework/searchallhomework",
          },
        ).then(function(res) {
          that.teacherhomeworkList=res.data.data;
        });
      },
      add(){
        let that = this;
        that.$axios(
          {
            method: "post",
            url: "/studenthomework/submit",
            data: that.studenthomework
          },
        ).then(function(res) {
          alert(res.data.data)
          that.drawer = false
          that.studenthomework.homeworkContent=''
        });
      },
      update(){
        let that = this;
        that.$axios(
          {
            method: "post",
            url: "/studenthomework/update",
            data: that.studenthomework
          },
        ).then(function(res) {
          alert(res.data.data)
          that.drawer = false
          that.studenthomework.homeworkContent=''
        });
      }
    }
  };
</script>



