<template>
  <div id="app">
    <div style="display: flex;justify-content: center;margin-top: 225px;">
      <el-card class="table_card">
        <div slot="header" class="clearfix">
          <span>作业管理系统登录</span>
        </div>
        <el-form
          :model="user"
          status-icon
          :rules="rules"
          ref="user"
          label-width="55px"
          class="demo-ruleForm"
        >
          <el-form-item label="ID:" placeholder="请输入邮箱" prop="userId">
            <el-input v-model="user.userId" autocomplete="off" number></el-input>
          </el-form-item>
          <el-form-item label="密码" placeholder="请输入密码" prop="userPassword">
            <el-input type="password" v-model="user.userPassword" show-password clearable></el-input>
          </el-form-item>
        </el-form>
        <div>
          <el-button type="success" @click="doLogin('user')" style="width:200px">登录</el-button>
           <el-button type="primary" @click="jumpOnly('/register')" style="width:130px">注册</el-button>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
export default {
  //单页面中不支持前面的data:{}方式
  data() {
    var checkId = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('id不为空'));
      }else {
        callback()
      }

    };
    var checkPass = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };
    return {
      user: {
        userId: '',
        userPassword: ''
      },
      radio:"1",
      rules: {
        userId: [{ validator: checkId, trigger: "blur" }],
        userPassword: [{ validator: checkPass, trigger: "blur" }]
      }
    };
  },
  methods: {
    doLogin(user) {
      //一点击登录按钮，这个方法就会执行
      let that = this;
      this.$refs[user].validate(function(valid) {
          //ajax请求
          that.$axios(
              {
                //请求方式
                method: "post",
                //请求路劲
                url: "/login",
                //请求参数
                data: that.user
                //请求成功的回调函数
              }
            )
            .then(function(res) {
                that.$notify({
                  message: res.data.data,
                });
                if(res.data.data=="登录失败"||res.data.data=="账号不存在或密码错误"||res.data.data=="账户异常"){

                }else {
                  that.jump("/main");
                }

            })
            .catch(function() {
              console.log("服务器异常");
            });
      });
    },
    jump(path) {
      this.$router.push({ path: path, query: { userId: this.user.userId } });
    },
    jumpOnly(path) {
      this.$router.push(path);
    }
  }
};
</script>

<style>
#app {
  height: 100%;
  position: fixed;
  width: 100%;
  top: 0px;
  right: 0px;
}
.collapse-btn {
  float: left;
  padding: 0 21px;
  cursor: pointer;
  line-height: 70px;
}
.table_card {
  background-color: #ffffffb0;
  width: 390px;
}
.el-col {
  border-radius: 4px;
}
.image {
  position: fixed;
  left: 10px;
  top: 80px;
}
</style>



