<template>
    <div class="nav" style="display: flex;flex-direction: row">
      <el-menu default-active="1-4-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" :collapse="isCollapse">
        <el-menu-item index="2">
          <i v-if="!isCollapse" @click="close" class="el-icon-s-fold"/>
          <i v-else @click="open" class="el-icon-s-unfold"/>
        </el-menu-item>

        <el-button type="text" @click="quit">{{this.user.userName}}</el-button>

        <el-submenu index="1" v-if="this.user.userType==0">
          <template slot="title">
            <i class="el-icon-user"></i>
            <span slot="title">学生</span>
          </template>
          <el-menu-item-group>
            <span slot="title"></span>
            <el-menu-item index="1-1" @click="jump('/handlehomework')">提交作业</el-menu-item>
            <el-menu-item index="1-2" @click="jump('/studenthomework')">作业提交记录</el-menu-item>
          </el-menu-item-group>
        </el-submenu>

        <el-submenu index="2" v-if="this.user.userType==1">
          <template slot="title">
            <i class="el-icon-s-custom"></i>
            <span slot="title">教师</span>
          </template>
          <el-menu-item-group>
            <span slot="title"></span>
            <el-menu-item @click="jump('/reviewhomework')" index="2-1">作业记录</el-menu-item>
            <el-menu-item @click="jump('/teacherhomework')" index="2-2">发布作业</el-menu-item>
            <el-menu-item @click="jump('/student')" index="2-3">学生列表</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
      <router-view/>
    </div>
</template>

<script>
    export default {
      data() {
        return {
          isCollapse: true,
          userId:'',
          user:{
            userId:'',
            userName:'',
            userType:'',
          },
          userlist:[]
        };
      },
      mounted() {
        let that=this
        that.userId = that.$route.query.userId
        that.user.userId = that.$route.query.userId
        that.get()
      },
      methods: {
        get(){
          let that = this;
          that.$axios(
            {
              method: "post",
              url: "/searchbyid",
              data:that.user
            },
          ).then(function(res) {
              that.userlist = res.data.data;
              that.user.userId = that.userlist[0].userId;
              that.user.userName = that.userlist[0].userName;
              that.user.userType= that.userlist[0].userType;
          });
        },
        close(){
          this.isCollapse = true;
        },
        quit(){
          let that=this
          this.$confirm('是否要退出').then(_ => {
              this.$router.push({ path: "/"})
            })
            .catch(_ => { });
        },
        open(){
          this.isCollapse = false;
        },
        handleOpen(key, keyPath) {
          console.log(key, keyPath);
        },
        handleClose(key, keyPath) {
          console.log(key, keyPath);
        },
        jump(path) {
          this.$router.push({ path: path,query:{userId:this.userId}});
        },
      }
    };
</script>

<style>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    margin-top: 0px;
    width: 200px;
    min-height: 400px;
    height: 100%;
  }
</style>



